ul {
  list-style: none;
}
h1,
h2,
h3 {
  margin: 0;
  padding: 0;
}
body {
  margin: 0 auto;
  font-family: "Roboto", sans-serif;
}
.container {
  max-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 76px;
  }
}
@media (min-width: 1024px) {
  .container {
    width: 1024px;
  }
}
